import { useEffect, useState } from "react";
import io from "socket.io-client";
import Slot from "./components/slot";
import { databaseRef } from "./firebase";
import { getDatabase, ref, get, child } from "firebase/database";

const socket = io.connect(process.env.REACT_APP_SERVER_URL);

function App() {
  const [messages, setMessages] = useState([]);
  const [slots, setSlots] = useState([
    { id: "PLT0001", status: false },
    { id: "PLT0002", status: false },

  ]);
  const dbRef = ref(getDatabase());
  get(child(dbRef, `/slot 1`)).then((snapshot) => {
    if (snapshot.exists()) {
     
      
      // Corrected usage of setSlots to update only the first slot
      setSlots(prevSlots => [
        { ...prevSlots[0], status: snapshot.val()},
        ...prevSlots.slice(1) // Keep the rest of the slots unchanged
      ]);
    } else {
      console.log("No data available");
    }
  }).catch((error) => {
    console.error(error);
  });

  
  return (
    <>
      <div className="text-center mt-5 pb-3">
        <h3>SLT - Car Park System</h3>
      </div>

      <div className="my-5">
        {/* <ul>
          {messages.length !== 0
            ? messages.map((message, index) => <li key={index}>{message}</li>)
            : "No messages"}
        </ul> */}
      </div>

      <div className="col-12 col-xl-6 mx-auto d-flex flex-wrap">
        {slots.map((plot) => (
          <Slot key={plot.id} slotId={plot.id} slotStatus={plot.status} />
        ))}
      </div>
    </>
  );
}

export default App;
